.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 20px;
}
.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 20px;
}

.dialog img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
    padding: 10px;
}